import React, { Component } from "react";
import $ from "jquery";
import "./App.scss";
import Header from "./components/Header";
import Footer from "./components/Footer";
import About from "./components/About";
import Experience from "./components/Experience";
import Projects from "./components/Projects";
import Skills from "./components/Skills";
import CountriesVisited from "./components/CountriesVisited";
import ChampionshipsCourses from "./components/ChampionshipsCourses";
import Education from "./components/Education";
import CertificationsCourses from "./components/CertificationsCourses";
import VisionMission from "./components/VisionMission";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      resumeData: null,
      sharedData: null,
      language: "en"
    };

    this.applyPickedLanguage = this.applyPickedLanguage.bind(this);
  }

  componentDidMount() {
    this.loadSharedData();
    this.applyPickedLanguage("en"); // Default language on load
  }

  loadResumeFromPath(path) {
    $.ajax({
      url: path,
      dataType: "json",
      cache: false,
      success: function (data) {
        this.setState({ resumeData: data });
      }.bind(this),
      error: function (xhr, status, err) {
        alert(err);
      },
    });
  }

  loadSharedData() {
    $.ajax({
      url: `portfolio_shared_data.json`,
      dataType: "json",
      cache: false,
      success: function (data) {
        this.setState({ sharedData: data }, () => {
          document.title = this.state.sharedData.basic_info ? this.state.sharedData.basic_info.name : "Portfolio";
        });
      }.bind(this),
      error: function (xhr, status, err) {
        alert(err);
      },
    });
  }

  applyPickedLanguage(language) {
    const resumePath = language === "en" ? "res_primaryLanguage.json" : "res_secondaryLanguage.json";
    this.loadResumeFromPath(resumePath);
    document.documentElement.lang = language;
    this.setState({ language });
  }

  render() {
    const { resumeData, sharedData, language } = this.state;

    if (!resumeData || !sharedData) {
      return <div>Loading...</div>;
    }

    return (
      <div>
        <Header
          sharedData={sharedData.basic_info}
          loadResumeFromPath={this.applyPickedLanguage}
          language={language}
        />
        
        <About resumeBasicInfo={resumeData.basic_info} sharedBasicInfo={sharedData.basic_info} />
        <Projects resumeProjects={resumeData.projects} resumeBasicInfo={resumeData.basic_info} />
        <Skills sharedSkills={sharedData.skills} resumeBasicInfo={resumeData.basic_info} />
        <Experience resumeExperience={resumeData.experience} resumeBasicInfo={resumeData.basic_info} />
        {resumeData.countries && <CountriesVisited countries={resumeData.countries} />}
        {resumeData.championships_courses && <ChampionshipsCourses championships_courses={resumeData.championships_courses} />}
        {resumeData.education && <Education education={resumeData.education} />}
        {resumeData.certifications_courses && <CertificationsCourses certifications={resumeData.certifications_courses} />}
        {resumeData.vision_mission && <VisionMission visionMission={resumeData.vision_mission} />}
        
        <Footer sharedBasicInfo={sharedData.basic_info} />
      </div>
    );
  }
}

export default App;