import React from "react";

const CountriesVisited = ({ countries }) => {
  // Define mapping for country flag emojis
  const countryFlags = {
    Turkey: "🇹🇷",
    Greece: "🇬🇷",
    Bulgaria: "🇧🇬",
    Hungary: "🇭🇺",
    Slovakia: "🇸🇰",
    Austria: "🇦🇹",
    "Czech Republic": "🇨🇿",
    Germany: "🇩🇪",
    Netherlands: "🇳🇱",
    Ireland: "🇮🇪",
    Belgium: "🇧🇪",
    France: "🇫🇷",
    Spain: "🇪🇸",
    Italy: "🇮🇹",
    Morocco: "🇲🇦",
    "United States": "🇺🇸",
  };

  return (
    <section id="countries-visited" style={{ textAlign: "center", marginTop: "20px" }}>
      <h2>Countries I've Visited</h2>
      <div style={{ display: "flex", flexWrap: "wrap", justifyContent: "center", gap: "15px", marginTop: "20px" }}>
        {countries.map((country) => (
          <div
            key={country}
            style={{
              display: "flex",
              alignItems: "center",
              fontSize: "1.2em",
              padding: "10px 15px",
              borderRadius: "8px",
              boxShadow: "0 4px 8px rgba(0, 0, 0, 0.1)",
              backgroundColor: "#f7f7f7"
            }}
          >
            <span style={{ fontSize: "1.5em", marginRight: "8px" }}>
              {countryFlags[country] || "🏳️"}
            </span>
            <span>{country}</span>
          </div>
        ))}
      </div>
    </section>
  );
};

export default CountriesVisited;