import React from 'react';

const ChampionshipsCourses = ({ championships_courses }) => {
  if (!championships_courses || championships_courses.length === 0) {
    return null; // Return nothing if data is not available
  }

  return (
    <section className="section-container" id="championships-courses">
      <h1 className="section-title">Championships and Courses</h1>
      <div className="content">
        {championships_courses.map((course, index) => (
          <div key={index} className="course-card">
            <h3>{course.title}</h3>
            <p>
              {course.date && <span>{course.date}</span>}
              {course.location && <span>, {course.location}</span>}
            </p>
            {course.description && <p>{course.description}</p>}
          </div>
        ))}
      </div>
    </section>
  );
};

export default ChampionshipsCourses;