import React, { Component } from "react";
import { Icon } from "@iconify/react";
import angularIcon from "@iconify/icons-logos/angular-icon";
import reactIcon from "@iconify/icons-logos/react";
import vueIcon from "@iconify/icons-logos/vue";

class About extends Component {
  render() {
    // Check if sharedBasicInfo and resumeBasicInfo props are present and extract data
    const profilepic = this.props.sharedBasicInfo
      ? "images/" + this.props.sharedBasicInfo.image
      : null;
    const sectionName = this.props.resumeBasicInfo
      ? this.props.resumeBasicInfo.section_name.about
      : "";
    const hello = this.props.resumeBasicInfo
      ? this.props.resumeBasicInfo.description_header
      : "";
    const about = this.props.resumeBasicInfo
      ? this.props.resumeBasicInfo.description
      : "";
    const socialLinks = this.props.sharedBasicInfo
      ? this.props.sharedBasicInfo.social
      : [];

    // Split the description text by double newlines to create paragraphs
    const aboutParagraphs = about ? about.split('\n\n') : [];

    return (
      <section id="about">
        <div className="col-md-12">
          <h1 style={{ color: "black" }}>
            <span>{sectionName}</span>
          </h1>
          <div className="row center mx-auto mb-5">
            <div className="col-md-4 mb-5 center">
              <div className="polaroid">
                <span style={{ cursor: "auto" }}>
                  <img
                    height="250px"
                    src={profilepic}
                    alt="Avatar placeholder"
                  />
                  {/* Example framework icons */}
                  <Icon
                    icon={angularIcon}
                    style={{ fontSize: "400%", margin: "9% 5% 0 5%" }}
                  />
                  <Icon
                    icon={reactIcon}
                    style={{ fontSize: "400%", margin: "9% 5% 0 5%" }}
                  />
                  <Icon
                    icon={vueIcon}
                    style={{ fontSize: "400%", margin: "9% 5% 0 5%" }}
                  />
                </span>
              </div>
            </div>

            <div className="col-md-8 center">
              <div className="col-md-10">
                <div className="card">
                  <div className="card-header">
                    <span
                      className="iconify"
                      data-icon="emojione:red-circle"
                      data-inline="false"
                    ></span>{" "}
                    &nbsp;{" "}
                    <span
                      className="iconify"
                      data-icon="twemoji:yellow-circle"
                      data-inline="false"
                    ></span>{" "}
                    &nbsp;{" "}
                    <span
                      className="iconify"
                      data-icon="twemoji:green-circle"
                      data-inline="false"
                    ></span>
                  </div>
                  <div
                    className="card-body font-trebuchet text-justify ml-3 mr-3"
                    style={{
                      height: "auto",
                      fontSize: "132%",
                      lineHeight: "200%",
                    }}
                  >
                    <br />
                    <span className="wave">{hello} :) </span>
                    <br />
                    <br />
                    {/* Render each paragraph */}
                    {aboutParagraphs.map((paragraph, index) => (
                      <p key={index} style={{ marginBottom: "1em" }}>
                        {paragraph}
                      </p>
                    ))}

                    {/* Social Media Links */}
                    <div className="social-icons mt-3">
                      {socialLinks.map((link, index) => (
                        <a
                          key={index}
                          href={link.url}
                          target="_blank"
                          rel="noopener noreferrer"
                          className="social-icon"
                          style={{ fontSize: "24px", marginRight: "15px" }}
                        >
                          <i className={link.class}></i>
                        </a>
                      ))}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    );
  }
}

export default About;