import React from "react";

const CertificationsCourses = ({ certifications }) => {
  if (!certifications) return null;

  return (
    <section id="certifications" className="pb-5">
      <h1 className="section-title" style={{ color: "black" }}>Certifications and Courses</h1>
      {certifications.map((cert, index) => (
        <div key={index} className="certification-item">
          <h3>{cert.title}</h3>
          {cert.organization && <p>{cert.organization}</p>}
          <p>{cert.year}</p>
        </div>
      ))}
    </section>
  );
};

export default CertificationsCourses;