import React, { Component } from "react";
import Typical from "react-typical";
import Switch from "react-switch";

class Header extends Component {
  titles = [];

  constructor() {
    super();
    this.state = { checked: false, language: "en" };
    this.onThemeSwitchChange = this.onThemeSwitchChange.bind(this);
    this.applyPickedLanguage = this.applyPickedLanguage.bind(this);
  }

  onThemeSwitchChange(checked) {
    this.setState({ checked });
    this.setTheme();
  }

  setTheme() {
    var dataThemeAttribute = "data-theme";
    var body = document.body;
    var newTheme =
      body.getAttribute(dataThemeAttribute) === "dark" ? "light" : "dark";
    body.setAttribute(dataThemeAttribute, newTheme);
  }

  applyPickedLanguage(language) {
    this.setState({ language });
    this.props.loadResumeFromPath(language); // Call the prop function to switch language data
    document.documentElement.lang = language; // Update the language attribute for accessibility
  }

  render() {
    if (this.props.sharedData) {
      var name = this.props.sharedData.name;
      this.titles = this.props.sharedData.titles.map(x => [ x.toUpperCase(), 1500 ] ).flat();
    }

    const HeaderTitleTypeAnimation = React.memo(() => {
      return <Typical className="title-styles" steps={this.titles} loop={50} />
    }, (props, prevProp) => true);

    return (
      <header id="home" style={{ height: window.innerHeight - 140, display: 'block' }}>
        <div className="row aligner" style={{height: '100%'}}>
          <div className="col-md-12">
            <div>
              <span className="iconify header-icon" data-icon="mdi:boxing-glove" data-inline="false"></span>
              <br/>
              <h1 className="mb-0">
                <Typical steps={[name]} wrapper="p" />
              </h1>
              <div className="title-container">
                <HeaderTitleTypeAnimation />
              </div>
              
              {/* Language Toggle with Flag Icons */}
              <div className="language-toggle" style={{ marginTop: "20px", display: "flex", justifyContent: "center", gap: "15px" }}>
                <span
                  onClick={() => this.applyPickedLanguage("en")}
                  className="iconify language-icon"
                  data-icon="twemoji:flag-for-flag-united-states"
                  data-inline="false"
                  style={{
                    cursor: "pointer",
                    fontSize: "2.5em", // Increased size for better visibility
                    filter: this.state.language === "en" ? "brightness(50%)" : "none"
                  }}
                ></span>
                <span
                  onClick={() => this.applyPickedLanguage("tr")}
                  className="iconify language-icon"
                  data-icon="twemoji:flag-for-flag-turkey"
                  data-inline="false"
                  style={{
                    cursor: "pointer",
                    fontSize: "2.5em", // Increased size for better visibility
                    filter: this.state.language === "tr" ? "brightness(50%)" : "none"
                  }}
                ></span>
              </div>

              <Switch
                checked={this.state.checked}
                onChange={this.onThemeSwitchChange}
                offColor="#baaa80"
                onColor="#353535"
                className="react-switch mx-auto"
                width={90}
                height={40}
                uncheckedIcon={
                  <span
                    className="iconify"
                    data-icon="twemoji:owl"
                    data-inline="false"
                    style={{
                      display: "block",
                      height: "100%",
                      fontSize: 25,
                      textAlign: "end",
                      marginLeft: "20px",
                      color: "#353239",
                    }}
                  ></span>
                }
                checkedIcon={
                  <span
                    className="iconify"
                    data-icon="noto-v1:sun-with-face"
                    data-inline="false"
                    style={{
                      display: "block",
                      height: "100%",
                      fontSize: 25,
                      textAlign: "end",
                      marginLeft: "10px",
                      color: "#353239",
                    }}
                  ></span>
                }
                id="icon-switch"
              />
            </div>
          </div>
        </div>
      </header>
    );
  }
}

export default Header;