import React from "react";

const VisionMission = ({ visionMission }) => {
  if (!visionMission) return null;

  return (
    <section id="vision-mission" className="pb-5">
      <h1 className="section-title" style={{ color: "black" }}>Vision and Mission</h1>
      <div className="vision">
        <h3>Vision</h3>
        <p>{visionMission.vision}</p>
      </div>
      <div className="mission">
        <h3>Mission</h3>
        <p>{visionMission.mission}</p>
      </div>
    </section>
  );
};

export default VisionMission;